import React from 'react';
import './Jumbo.css';

const Jumbo = props => (

  <div className="jumbotron parallax">
  <div className="jumbotron top">
    <h1 className="display-3">Ready to play?</h1>
  </div>
    <h3 className="text-center">Try to figure out the logic of the game!<span id="hint"> Hint: pay attention to your score!</span></h3>
  </div>

);

export default Jumbo;



